.root {
  display: flex;
  justify-content: center;
  padding-left: 10px;
  li {
    display: inline-flex;
  }

  a {
    color: rgb(49, 33, 49);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border: 1px solid rgb(75, 52, 78);
    border-radius: 30px;
    margin-right: 10px;
    cursor: pointer;

    &:hover {
      background-color: rgb(111, 76, 115);
      color: #fff;
    }
  }

  :global {
    .selected {
      a {
        background-color: rgb(75, 52, 78);
        color: #fff;
      }
    }
  }
}
